@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Quicksand&display=swap);
.available_ingredients{
    height: auto;
    width: 60%;
    align-content: center;
    background: #FDFDFD;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.06);
    border-radius: 8px;
    display: block;
    margin: 10px auto auto auto;
    font-family:'Times New Roman';
    padding: 10px auto 5px 10px;
    padding-left: 3vh;
    padding-bottom: 3vh;


    
}
.title{
    display:flex;
    flex-direction: row;

}

button{
    cursor: pointer;
    
}
.Selected_Ingredients{
    height: auto;
    width: 60%;
    align-content: center;
    background: #FDFDFD;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.06);
    border-radius: 8px;
    display: block;
    margin: 10px auto auto auto;
    padding-left: 3vh;
    padding-bottom: 3vh;
}
.table{
    width: 60%;
    display: grid;
    height: 50px;
    grid-template-columns:  30% 30% 30%;
    align-items: center;
    align-content: center;
    justify-items: center;
    transition: -webkit-transform 1.0s;
    transition: transform 1.0s;
    transition: transform 1.0s, -webkit-transform 1.0s;
    box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.06);
    border-radius: 8px;
    margin-bottom: 10px;
    
}
.summary{
    height: auto;
    width: 60%;
    align-content: center;
    background: #FDFDFD;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.06);
    border-radius: 8px;
    display: block;
    margin: 10px auto auto auto;
    padding-left: 3vh;
    padding-bottom: 3vh;
    padding-top: 2vh;
}

